<style>
  .ll-p-m-w {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
  }
  .ll-img-w {
    width: 60px;
    height: 60px;
  }
  .ll-img-w>img {
    width: 60px;
  }
  .ll-p-m-c-r {
    padding: 0 10px;
  }
  .ll-erp-p-no {
    color: #666;
    font-size: 12px;
  }
  .c-g-sfw {
    padding-top: 0 !important;
    padding-bottom: 0 !important;
  }

  .store-check-wraper {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
  }
  .check-store-item {
    position: relative;
    border: 1px solid #ececec;
    border-radius: 6px;
    padding: 0 20px 0 12px;
    margin: 10px 15px 0 0;
    height: 36px;
    line-height: 36px;
    background: #ececec;
  }
  .check-store-item:hover {
    border: 1px solid #3080fe;
  }
  .del-store-icon-w {
    position: absolute;
    top: -8px;
    right: 2px;
    cursor: pointer;
  }
</style>

<template>
  <div class="add-form-wraper">
    <el-form ref="form" :model="addParamMesg" label-width="160px">
      <el-form-item label="预售周期名称" required>
        <el-input v-model="addParamMesg.activityName" size="small" class="add-input" placeholder="请输入预售周期名称"></el-input>
        <span>活动名称，限制20个字</span>
      </el-form-item>

      <el-form-item label="开始时间" required>
        <el-date-picker
          class="add-input"
          size="small"
          v-model="addParamMesg.startDate"
          type="datetime"
          format="yyyy.MM.dd HH:mm:ss"
          value-format="yyyy.MM.dd HH:mm:ss"
          :picker-options="pickerOptions"
          placeholder="请输入开始时间">
        </el-date-picker>
        <span>长周期预售不能2期同时进行，多个长周期预售，起止时间不能重合，时间添加保存后不可修改</span>
      </el-form-item>

      <el-form-item label="结束时间" required>
        <el-date-picker
          class="add-input"
          size="small"
          v-model="addParamMesg.endDate"
          type="datetime"
          format="yyyy.MM.dd HH:mm:ss"
          value-format="yyyy.MM.dd HH:mm:ss"
          :picker-options="pickerOptions"
          placeholder="请输入结束时间">
        </el-date-picker>
        <span>长周期预售不能2期同时进行，多个长周期预售，起止时间不能重合，时间添加保存后不可修改</span>
      </el-form-item>

      <el-form-item label="提货日期" required>
        <el-date-picker
          class="add-input-inline"
          size="small"
          v-model="addParamMesg.pickUpStartDate"
          type="date"
          format="yyyy.MM.dd"
          value-format="yyyy.MM.dd"
          :picker-options="pickerOptions"
          placeholder="开始日期">
        </el-date-picker>
        -
        <el-date-picker
          class="add-input-inline"
          size="small"
          v-model="addParamMesg.pickUpEndDate"
          type="date"
          format="yyyy.MM.dd"
          value-format="yyyy.MM.dd"
          :picker-options="pickerOptions"
          placeholder="结束日期">
        </el-date-picker>
        <span>提货日期添加保存后不可修改</span>
      </el-form-item>

      <el-form-item label="参与门店" required>
        <el-button size="small" @click="chooseStoreOpen" icon="el-icon-plus">选择门店</el-button>
        <div v-if="activityStoresCheckedList.length > 0" class="store-check-wraper">
          <div v-for="(item, idx) in activityStoresCheckedList" :key="idx" class="check-store-item">
            {{item.erpStoreNo}}_{{item.erpStoreName}}({{item.saleTypeName}})
            <span class="del-store-icon-w" @click="deleteStoreItem(item)">
              <i class="el-icon-error"></i>
            </span>
          </div>
        </div>
      </el-form-item>

      <el-form-item label="设置预售商品" required>
        <el-button type="primary" @click="goAddGoodsDialog" size="mini" icon="el-icon-plus">添加商品</el-button>
        
        <el-table
          class="fixed-height"
          :data="checkedList"
          empty-text="暂无选中任何商品"
          style="width:100%;"
          size="small">
          <el-table-column width="226px" label="商品信息">
            <template slot-scope="scope">
              <div class="ll-p-m-w">
                <div class="ll-img-w">
                  <img v-if="scope.row.imgUrl && scope.row.imgUrl != ''" :src="scope.row.imgUrl" alt="" onerror="onerror=null;src='/shop/static/img_error.png'">
                  <img v-else src="/shop/static/img_error.png" alt="">
                </div>
                <div class="ll-p-m-c-r">
                  <div>
                    <el-input v-model="scope.row.goodsName" size="small" placeholder="输入商品名称"></el-input>
                  </div>
                  <div class="ll-erp-p-no">SKU编码：{{scope.row.goodsNo || '--'}}</div>
                </div>
              </div>
            </template> 
          </el-table-column>
          <el-table-column width="120px" prop="spec" label="规格"></el-table-column>
          <el-table-column width="130px" label="原价(必填)">
            <template slot-scope="scope">
              <div>
                <el-input v-model="scope.row.price" size="small" placeholder="输入原价"></el-input>
              </div>
            </template>
          </el-table-column>
          <el-table-column width="130px" label="预售价(必填)">
            <template slot-scope="scope">
              <div>
                <el-input v-model="scope.row.presalePrice" size="small" placeholder="输入预售价"></el-input>
              </div>
            </template>
          </el-table-column>
          <el-table-column min-width="130px" :render-header="platformCurStockHeader">
            <template slot-scope="scope">
              <div>
                <el-input v-model="scope.row.platformCurStock" size="small" placeholder="区域限售库存"></el-input>
              </div>
            </template>
          </el-table-column>
          <el-table-column width="130px" label="门店限售库存">
            <template slot-scope="scope">
              <div>
                <el-input v-model="scope.row.stock" size="small" placeholder="门店限售库存"></el-input>
              </div>
            </template>
          </el-table-column>
          <el-table-column width="160px" prop="marketPrice" label="每人限购(为空时不限)">
            <template slot-scope="scope">
              <div>
                <el-input v-model="scope.row.memberLimitNum" size="small" placeholder="输入每人限购"></el-input>
              </div>
            </template>
          </el-table-column>
          <el-table-column
            align="center"
            label="操作"
            width="80">
            <template slot-scope="scope" size="small">
              <el-button @click="deleteItem(scope.row)" type="text" size="mini" icon="el-icon-delete">删除</el-button>
            </template>
          </el-table-column>
        </el-table>
        <p><span style="color:red;">*</span>长周期未开始时可以编辑商品，进行中后不可以再编辑商品；</p>
      </el-form-item>
      
      <hr class="s-slip-tb">
      <el-form-item>
        <el-button type="primary" size="small" @click="doSaveHandel" :disabled="isDisabled">保存</el-button>
        <el-button size="small" @click="doCancelHandel">取消</el-button>
      </el-form-item>
    </el-form>
    
    <!-- 选择商品 -->
    <choose-goods ref="chooseGoods" @confirmChooseGoods="confirmChooseGoods"></choose-goods>
    <!-- 选择门店 -->
    <choose-store ref="chooseStore" @confirmChooseStore="confirmChooseStore"></choose-store>
  </div>
</template>

<script>
  import Storage from '@/utils/localStorage'
  import * as utils from '@/utils/utils'
  import ChooseGoods from "./ChooseGoods"
  import ChooseStore from "./ChooseStore"
  const presaleGoodsMgt = {
    // 保存活动
    saveActivity: '/plateform/a/mall/saleActivity/saveActivity'
  }
  export default {
    data() {
      return {
        isDisabled: false,
        addParamMesg: {
          platformId: 5, // 平台类型：1-有赞；2-京东；3-美团；4-饿百；5-商城
          activityName: '', // 活动名称
          activityName: '', // 活动名称
          startDate: '', // 开始时间
          endDate: '', // 结束时间
          pickUpStartDate: '', // 提货开始时间
          pickUpEndDate: '', // 提货结束时间
          goodsReqList: [],
          storeNoList: []
        },
        pickerOptions: {
          disabledDate(time) {
            return time.getTime() < Date.now() - 8.64e7
          }
        },
        // 选中的商品列表
        checkedList: [],
        // 参与门店选中的
        activityStoresCheckedList: []

      }
    },
    components: {
      "choose-goods": ChooseGoods,
      "choose-store": ChooseStore
    },
    mounted() {

    },
    methods: {
      /**
       * [doSaveHandel 保存]
       * @return {[type]} [description]
       */
      doSaveHandel() {
        let numReg = /^[1-9]*[1-9][0-9]*$/
        let salePriceReg = /((^[1-9]\d*)|^0)(\.\d{0,2}){0,1}$/
        if(!this.addParamMesg.activityName) {
          this.$message("请输入预售周期名称")
          return
        }else {
          if(this.addParamMesg.activityName.length > 20) {
            this.$message("预售周期名称,限制20个字")
            return
          }
        }
        // 起止日期
        if(!this.addParamMesg.startDate) {
          this.$message("请输入起止时间的开始日期")
          return
        }
        if(!this.addParamMesg.endDate) {
          this.$message("请输入起止时间的结束日期")
          return
        }
        let startDateNum = Date.parse(new Date(this.addParamMesg.startDate))
        let endDateNum = Date.parse(new Date(this.addParamMesg.endDate))
        if(endDateNum <= startDateNum) {
          this.$message("起止时间结束日期不能早于或等于开始日期")
          return
        }
        // 提货起止时间
        if(!this.addParamMesg.pickUpStartDate) {
          this.$message("请输入提货日期的开始日期")
          return
        }
        let pickUpStartDateNum = Date.parse(new Date(this.addParamMesg.pickUpStartDate))
        if(pickUpStartDateNum <= endDateNum) {
          this.$message("提货日期不能早于起止时间")
          return
        }
        if(!this.addParamMesg.pickUpEndDate) {
          this.$message("请输入提货日期的结束日期")
          return
        }
        let pickUpEndDateNum = Date.parse(new Date(this.addParamMesg.pickUpEndDate))
        if(pickUpEndDateNum < pickUpStartDateNum) {
          this.$message("提货日期结束日期不能早于开始日期")
          return
        }

        let storeNoList = this.activityStoresCheckedList.map(item => {
          return item.erpStoreNo
        })
        this.addParamMesg.storeNoList = storeNoList
        if(this.addParamMesg.storeNoList.length === 0) {
          this.$message("请选择参与门店")
          return
        }
        let goodsReqListArr = []
        for(let item of this.checkedList) {
          if(item.goodsName === '') {
            this.$message(`请输入(${item.goodsName})商品名称`)
            return
          }
          if(!item.price || item.price == 0) {
            this.$message(`请输入(${item.goodsName})的原价`)
            return
          }else {
            if(!salePriceReg.test(item.price)) {
              this.$message('原价是大于0，小数保留小数点后2位') 
              return
            }
          }
          if(!item.presalePrice || item.presalePrice == 0) {
            this.$message(`请输入(${item.goodsName})的预售价`)
            return
          }else {
            if(!salePriceReg.test(item.presalePrice)) {
              this.$message('预售价是大于0，小数保留小数点后2位') 
              return
            }
          }

          if(parseFloat(item.presalePrice) > parseFloat(item.price)) { 
            this.$message(`(${item.goodsName})的原价必须大于预售价`)
            return
          }

          if(item.platformCurStock != 0 && item.platformCurStock) {
            if(numReg.test(item.platformCurStock)) {
              if(item.platformCurStock < 0) {
                this.$message("区域限售库存不能小于0")
                return
              }else if(item.platformCurStock > 9999) {
                this.$message("区域限售库存不能大于9999")
                return
              }
            }else {
              this.$message("门店限售库存格式不正确")
              return
            }
          }

          if(item.stock != 0 && item.stock) {
            if(numReg.test(item.stock)) {
              if(item.stock < 0) {
                this.$message("门店限售库存不能小于0")
                return
              }
            }else {
              this.$message("门店限售库存格式不正确")
              return
            }
          }

          if(item.memberLimitNum != 0 && item.memberLimitNum) {
            if(numReg.test(item.memberLimitNum)) {
              if(item.memberLimitNum < 0) {
                this.$message("限购数量不能小于0")
                return
              }
            }else {
              this.$message("输入限购数量格式不正确")
              return
            }
          }

          let goodsReqListItem = {
            goodsId: item.goodsId,
            goodsName: item.goodsName,
            presalePrice: item.presalePrice,
            price: item.price,
            stock: item.stock,
            platformCurStock: item.platformCurStock,
            memberLimitNum: item.memberLimitNum
          }
          goodsReqListArr.push(goodsReqListItem)
        }

        this.addParamMesg.goodsReqList = goodsReqListArr
        if(this.addParamMesg.goodsReqList.length === 0) {
          this.$message("请先设置预售商品")
          return
        }

        this.isDisabled = true
        this.$http.post(presaleGoodsMgt.saveActivity, {...this.addParamMesg}).then(res => {
          // 保存按钮可再次点击
          setTimeout(() => {
            this.isDisabled = false
          }, 800)
          if(res.code === 0) {
            this.$message.success("保存成功")
            this.$router.push('/long_pgm')
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.isDisabled = false
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })
      },
      /**
       * [doCancelHandel 取消]
       * @return {[type]} [description]
       */
      doCancelHandel() {
        this.$confirm('确定取消吗?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.$router.push('/long_pgm')
        }).catch(() => {})
      },
      /**
       * [goAddGoodsDialog 添加商品]
       * @return {[type]} [description]
       */
      goAddGoodsDialog() {
        if(this.$refs.chooseGoods) {
          let _params = {
            title: '选择商品',
            checkedList: this.checkedList
          }
          this.$refs.chooseGoods.setDefault(_params)
        }
      },
      /**
       * [confirmChooseGoods 选中商品并关闭]
       * @param  {[type]} _val [description]
       * @return {[type]}      [description]
       */
      confirmChooseGoods(_val) {
        if(_val) {
          this.checkedList = _val.checkedList
        }
      },
      /**
       * [deleteItem 删除]
       * @param  {[type]} _row [description]
       * @return {[type]}      [description]
       */
      deleteItem(_row) {
        let uniqueId = _row.uniqueId
        let resCheckedListArr = this.checkedList.filter(item => {
          return item.uniqueId != uniqueId
        })

        this.checkedList = JSON.parse(JSON.stringify(resCheckedListArr))
      },
      platformCurStockHeader() {
        return (
          <el-popover
            placement="top-start"
            width="260"
            trigger="hover">
            <div>
              <p>区域限售库存，即所有门店共享的总预售库存；</p>
              <p>门店限售即每个门店的预售库存，可在门店预售商品中调整门店库存；</p>
              <p>每个商品至少要设置一种限售库存方式，最大可设置9999；</p>
              <p>如果既设了区域限售，又设了门店限售，则先校验区域库存，再校验门店库存；</p>
            </div>
            <span slot="reference">区域限售库存<i class="el-icon-question"></i></span>
          </el-popover>
        )
      },
      /**
       * [chooseStoreOpen 选择门店]
       * @return {[type]} [description]
       */
       chooseStoreOpen() {
        let _params = {
          title: '选择门店',
          checkedList: this.activityStoresCheckedList
        }
        this.$refs.chooseStore.setDefault(_params)
      },
      /**
       * [deleteItem 删除选中的门店]
       * @param  {[type]} _row [description]
       * @return {[type]}      [description]
       */
      deleteStoreItem(_row) {
        let erpStoreNo = _row.erpStoreNo
        this.activityStoresCheckedList = this.activityStoresCheckedList.filter(item => {
          return item.erpStoreNo !== erpStoreNo
        })
      },
      /**
       * [confirmChooseStore 选中的门店]
       * @param  {[type]} _value [description]
       * @return {[type]}        [description]
       */
      confirmChooseStore(_value) {
        let activityStoresCheckedList = []
        if(_value.checkedList && _value.checkedList.length) {
          activityStoresCheckedList = _value.checkedList
        }else {
          activityStoresCheckedList = []
        }
        this.activityStoresCheckedList = activityStoresCheckedList
      }

    }
  }
</script>


