<style>
  .c-g-sfw {
    padding-top: 0 !important;
    padding-bottom: 0 !important;
  }
</style>

<template>
  <div>
    <el-dialog 
      :title="title" 
      :visible.sync="isShow" 
      :close-on-click-modal = "false"
      :close-on-press-escape = "false"
      width='620px'>
      <div class="search-form-wraper c-g-sfw">
        <el-form :model="searchContent" label-position="top">
          <el-form-item>
            <el-input
              class="add-input-inline"
              placeholder="门店编号/名称"
              v-model="searchContent.searchValue"
              @keyup.enter.native="doSearchHandel"
              clearable
              size="small">
            </el-input>
          </el-form-item>
          <el-form-item>
            <el-select
              v-model="searchContent.saleType"
              size="small" 
              class="search-input-small-width"
              clearable
              placeholder="门店类型"
              @change="doSearchHandel">
              <el-option
                v-for="item in saleTypeOptions"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-cascader
            ref="cascaderCategory"
              placeholder="店组类型"
              class="add-input-inline"
              :options="storeGroupTypeNoOptions"
              size="small"
              filterable
              clearable
              :props="defaultProps"
              @change="storeGroupTypeNoChange"
              v-model="searchContent.storeGroupTypeNoList">
            </el-cascader>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="doSearchHandel" size="mini">搜索</el-button>
          </el-form-item>
        </el-form>
      </div>
      
      <div>
        <el-table
          class="fixed-height"
          ref="multipleTable"
          v-loading="loading"
          :data="tableData"
          empty-text="没有查询到您想要的数据"
          style="width:100%;"
          height="270"
          @select="handleSelect"
          @select-all="handleSelectAll"
          size="small">
          <el-table-column
            type="selection"
            align="center">
          </el-table-column>
          <!-- <el-table-column min-width="70" prop="storeId" label="门店ID"></el-table-column> -->
          <el-table-column min-width="170" label="门店编码/名称">
            <template slot-scope="scope">
              <div>
                {{scope.row.erpStoreNo}}_{{scope.row.erpStoreName}}
              </div>
            </template>     
          </el-table-column>
          
          <el-table-column min-width="100" :formatter="saleTypeFormatter" prop="saleType" label="门店类型"></el-table-column>
        </el-table>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="isShow = false" size="mini">取 消</el-button>
        <el-button type="primary" @click="confirmClose" size="mini" :disabled="checkedList.length === 0">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  import * as utils from '@/utils/utils'
  const reqUrl = {
    // 活动规则选择门店
    selectPlatfromStore: '/plateform/a/baseCommon/store/selectPlatfromStore',
    // 获取店组信息
    getStoreGroupType: '/backstage/storeGroup/storeGroupTypeList'
  }
  export default {
    data() {
      return {
        isShow: false,
        title: '', // 弹窗title
        loading: false,
        searchContent: {
          searchValue: '',
          saleType: '',
          platform: 5,
          storeGroupTypeNo: '',
          storeGroupNo: '',
          storeGroupTypeNoList: []
        },
        saleTypeOptions: [
          {
            label: '直营店',
            value: 1
          },
          {
            label: '加盟店',
            value: 2
          },
          {
            label: '分销店',
            value: 3
          },
          {
            label: '自提店',
            value: 4
          }
        ],
        storeGroupTypeNoOptions: [],
        defaultProps: {
          children: 'storeGroupList',
          label: 'labelName',
          value: 'valueName'
        },
        tableData: [],
        // 选中
        checkedList: [],
        otherCheckedList: [],
        groupIndex: 0
      }
    },
    methods: {
      /**
       * [setDefault 设置基本选中]
       * @param {[type]} _val [description]
       */
      setDefault(_val) {
        if(_val) {
          this.searchContent = {
            searchValue: '',
            saleType: '',
            platform: 5,
            storeGroupTypeNo: '',
            storeGroupNo: '',
            storeGroupTypeNoList: []
          }
          
          this.title = _val.title
          this.checkedList = JSON.parse(JSON.stringify(_val.checkedList)) 
          if(_val.otherCheckedList && _val.otherCheckedList.length) {
            this.otherCheckedList = [..._val.otherCheckedList]
          }
          this.groupIndex = _val.groupIndex || 0
        }

        this.isShow = true
        this.getStoreGroupType()
        this.doSearchHandel(true)
      },
      storeGroupTypeNoChange(_val) {
        this.searchContent.storeGroupTypeNo = _val[0]
        this.searchContent.storeGroupNo = _val[1]
        this.doSearchHandel(true)
      },
      /**
       * @description: 获取店组信息
       * @param {type} 
       * @return: 
       */
      getStoreGroupType() {
        this.$http.get(reqUrl.getStoreGroupType).then(res => {
          if(res.code === 0) {
            let dataList = res.data && res.data.length ? res.data: []
            this.modifyStoreGroupeTree(dataList)
            this.storeGroupTypeNoOptions = dataList
            return 
          }
          throw new Error(res.msg)
        }).catch(e => {
          console.log("【提示】获取店组信息失败：" + e.message)
        })
      },
      modifyStoreGroupeTree(storeTree, typeNo) {
        storeTree.forEach(v => {
          if(v.storeGroupList && v.storeGroupTypeNo) {
            // 一级分类
            v.valueName = v.storeGroupTypeNo
            v.labelName = v.storeGroupTypeName ? v.storeGroupTypeNo + '.' + v.storeGroupTypeName : ''
            if(v.storeGroupList.length) {
              this.modifyStoreGroupeTree(v.storeGroupList, v.storeGroupTypeNo)
            }
          }else if(v.storeGroupNo) {
            // 非一级分类
            v.valueName = v.storeGroupNo
            v.labelName = v.storeGroupName ? v.storeGroupNo + '.' + v.storeGroupName : ''
            if(v.childrenList && v.childrenList.length) {
              v.storeGroupList = JSON.parse(JSON.stringify(v.childrenList))
              this.modifyStoreGroupeTree(v.storeGroupList)
            }
          }
        })
      },
      /**
       * [doSearchHandel 查询列表接口]
       * @return {[type]}         [description]
       */
      doSearchHandel() {
        this.loading = true
        this.$http.post(reqUrl.selectPlatfromStore, {...this.searchContent}).then(res => {
          this.loading = false
          if(res.code === 0) {
            // 获取表格数据
            let tableDataTemp = res.data && res.data.length ? res.data : []
            this.tableData = utils.getSubtract(tableDataTemp, this.otherCheckedList, 'erpStoreNo')
            this.defaultSelectFun()
            return 
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.loading = false
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })
      },
      /**
       * [handleSelect 选择某一个]
       * @return {[type]} [description]
       */
      handleSelect(val, row) {
        let hash = {}
        let checkedListTemp = []

        if(utils.isKeyInArrayBykey(row.erpStoreNo, 'erpStoreNo', this.checkedList)) {
          this.checkedList = utils.removeItemByKeyVal('erpStoreNo', row.erpStoreNo, this.checkedList)
          return
        }else {
          checkedListTemp.push(row)
        }

        let checkedListTempArr = [...checkedListTemp, ...this.checkedList]
        this.checkedList = checkedListTempArr.reduceRight((item, next) => {
          hash[next.erpStoreNo] ? '' : hash[next.erpStoreNo] = true && item.push(next)
          return item
        }, [])
      },
      /**
       * [handleSelectAll 选择多个]
       * @return {[type]} [description]
       */
      handleSelectAll(val) {
        let hash = {}
        if(val.length > 0) {        
          // 全选
          for(let item of val) {
            this.checkedList.push(item)
          }
          // 去重
          this.checkedList = this.checkedList.reduceRight((item, next) => {
            hash[next.erpStoreNo] ? '' : hash[next.erpStoreNo] = true && item.push(next)
            return item
          }, [])
        }else { 
          // 全不选 此时val.length<=0
          // 1.获取当前table中每一条的id
          let checkedListTemp = []
          for(let item of this.tableData) {
            checkedListTemp.push(item)
          }
          // 2.从要传递的参数中去掉这些重复的
          let checkedListArr = utils.getSubtract(this.checkedList, checkedListTemp, 'erpStoreNo')
          this.checkedList = checkedListArr && checkedListArr.length > 0 ? checkedListArr : [] 
        }
      },
      /**
       * [defaultSelectFun 设置表格中的默认选中项]
       * @param    {[type]}                 row [description]
       * @return   {[type]}                     [description]
       */
      defaultSelectFun() {
        let _this = this
        let rows = []
        for(let item of this.tableData) {
          for(let key of this.checkedList) {
            if(item.erpStoreNo === key.erpStoreNo) {
              rows.push(item)
            }
          }            
        }
        if (rows.length > 0) {
          this.$refs.multipleTable.clearSelection()
          setTimeout(function() {
            rows.forEach(rowItem => {
              _this.$refs.multipleTable.toggleRowSelection(rowItem)
            });
          }, 100)
        } else {
          this.$refs.multipleTable.clearSelection()
        }
      },
      /**
       * [confirmClose 选择并导入]
       * @return {[type]} [description]
       */
      confirmClose() {
        let _params = {
          checkedList: this.checkedList,
          groupIndex: this.groupIndex
        }
        this.$emit("confirmChooseStore", _params)

        this.isShow = false
      },
      saleTypeFormatter(row, column, cellValue) {
        return this.saleTypeOptions.filter(item => {
          return item.value === cellValue
        })[0].label
      }

    }
  }
</script>



