<style>
  .ll-p-m-w {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
  }
  .ll-img-w {
    width: 60px;
    height: 60px;
  }
  .ll-img-w>img {
    width: 60px;
  }
  .ll-p-m-c-r {
    padding: 0 10px;
  }
  .ll-erp-p-no {
    color: #666;
    font-size: 12px;
  }
  .c-g-sfw {
    padding-top: 0 !important;
    padding-bottom: 0 !important;
  }
</style>

<template>
  <div>
    <el-dialog :title="title" :visible.sync="isShow" width='720px'>
      <div class="search-form-wraper c-g-sfw">
        <el-form :model="searchContent" label-position="top">
          <el-form-item>
            <el-cascader
              placeholder="请选择分类"
              class="search-input"
              :options="categoryList"
              change-on-select
              size="small"
              clearable
              filterable
              @change="doCategorysChangeHandel"
              :props="cateDefaultProps"
              v-model="searchContent.categoryIdList">
            </el-cascader>
          </el-form-item>
          <el-form-item>
            <goods-select ref="GoodsSelect" @DoSelectGoodsHandel="DoSelectGoodsHandel"></goods-select>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="doSearchHandel(true)" size="mini">搜索</el-button>
          </el-form-item>
        </el-form>
      </div>
      
      <div>
        <el-table
          class="fixed-height"
          ref="multipleTable"
          v-loading="loading"
          :data="tableData"
          empty-text="没有查询到您想要的数据"
          style="width:100%;"
          height="300"
          @select="handleSelect"
          @select-all="handleSelectAll"
          size="small">
          <el-table-column
            :selectable="selectable"
            type="selection"
            width="50">
          </el-table-column>
          <el-table-column min-width="226px" label="商品信息">
            <template slot-scope="scope">
              <div class="ll-p-m-w">
                <div class="ll-img-w">
                  <img v-if="scope.row.imgUrl && scope.row.imgUrl != ''" :src="scope.row.imgUrl" alt="" onerror="onerror=null;src='/shop/static/img_error.png'">
                  <img v-else src="/shop/static/img_error.png" alt="">
                </div>
                <div class="ll-p-m-c-r">
                  <div>{{scope.row.skuName || '--'}}</div>
                  <div class="ll-erp-p-no">SKU编码：{{scope.row.goodsNo}}</div>
                </div>
              </div>
            </template>     
          </el-table-column>
          <el-table-column min-width="120px" prop="spec" label="规格"></el-table-column>
          <el-table-column min-width="120px" prop="categoryName" label="分类"></el-table-column>
        </el-table>
        <!-- 分页条 -->
        <el-pagination
          v-show="tableData.length>0"
          class="tc mt20"
          background
          @size-change="changeSize"
          @current-change="changeCurrent"
          :current-page.sync="pageNum"
          :page-sizes="[10, 50, 100, 150, 200]"
          layout="total, sizes, prev, pager, next, jumper"
          :pager-count="5"
          :total="total">
        </el-pagination>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="isShow = false" size="mini">取 消</el-button>
        <el-button type="primary" @click="confirmClose" size="mini" :disabled="checkedList.length === 0">导 入</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  import * as utils from '@/utils/utils'
  import GoodsSelect from "@/components/Common/GoodsSelectByGid"
  const presaleGoodsMgt = {
    // 获取三方平台店内分类
    getCategoryTree: '/plateform/a/category/getCategoryTree',
    // 商城商品(News)——商品库管理
    librarySearch: '/plateform/a/product/goods/search',
    // 商品选择-商品列表
    selectGoods: '/plateform/a/product/goods/selectGoods'
  }
  export default {
    data() {
      return {
        isShow: false,
        title: '选择商品', // 弹窗title
        loading: false,
        pageNum: 1,
        pageSize: 10,
        total: 0,
        searchContent: {
          queryString: '', // 输入的查询条件
          // 是否根据城市刷选 true是 false否
          isByCityId: true,
          goodsIds: [], // 全国商品ID 集合
          categoryIdList: [],
          categoryId: '' // 商品分类ID
        },
        tableData: [],
        // 选中商品
        checkedList: [],
        // 默认分类参数
        cateDefaultProps: {
          children: 'categoryVoList',
          label: 'categoryName',
          value: 'categoryId'
        },
        categoryList: [] // 商品分类id

      }
    },
    components: {
      "goods-select": GoodsSelect
    },
    methods: {
      /**
       * [setDefault 设置基本选中]
       * @param {[type]} _val [description]
       */
      setDefault(_val) {
        if(_val) {
          this.title = _val.title
          this.checkedList = JSON.parse(JSON.stringify(_val.checkedList)) 
        }

        this.isShow = true
        // 获取三方平台分类
        this.getCategoryId()

        this.doSearchHandel(true)

        // 商品筛选组件初始化
        setTimeout(() => {
          this.goodsSelectInit()
        }, 200)
      },
      /**
       * [getCategoryId 三方平台店内分类]
       * @return {[type]} [description]
       */
      getCategoryId() {
        this.$http.post(presaleGoodsMgt.getCategoryTree).then(res => {
          if(res.code === 0) {
            if(res.data && res.data.length) {
              for(let item of res.data) {
                if(item.categoryVoList && item.categoryVoList.length === 0) {
                  item.categoryVoList = null
                }
              }
              this.categoryList = res.data
            }else {
              this.categoryList = []
            }
            return 
          }
          throw new Error(res.msg)
        }).catch(e => {
          console.log("提示(三方平台店内分类):", e.message)
        })
      },
      /**
       * [changeSize description]
       * @param    {[type]}                 value [description]
       * @return   {[type]}                       [description]
       */
      changeSize (value) {
        this.pageSize = value
        this.doSearchHandel()
      },
      /**
       * [changeCurrent 点击分页条]
       * @param    {[type]}                 value [当前页]
       * @return   {[type]}                       [description]
       */
      changeCurrent (value) {
        this.pageNum = value
        this.doSearchHandel()
      },
      /**
       * [doSearchHandel 查询列表接口]
       * @param  {[type]} _param [description]
       * @return {[type]}         [description]
       */
      doSearchHandel(_param) {
        let spliceLen = this.searchContent.categoryIdList.length - 1
        this.searchContent.categoryId = this.searchContent.categoryIdList[spliceLen]
        this.loading = true
        if(_param === true) {
          this.pageNum = 1
        }
        this.$http.post(presaleGoodsMgt.librarySearch, { ...this.searchContent, pageNum: this.pageNum, pageSize: this.pageSize }).then(res => {
          this.loading = false
          if(res.code === 0) {
            // 获取表格数据
            if(res.data && res.data.result && res.data.result.length) {
              for(let item of res.data.result) {
                item.uniqueId = `${item.id}` + `${item.goodsNo}`
                item.goodsId = item.id
                item.goodsName = item.skuName
                item.price = ''
                item.presalePrice = ''
                item.stock = ''
                item.platformCurStock = item.platformCurStock || ''
                item.memberLimitNum = ''
              }
              this.tableData = res.data.result
            }else {
              this.tableData = []
            }

            this.defauleSelectFun()

            if(this.pageNum == 1) {
              this.total = res.data.total
            }
            return 
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.loading = false
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })
      },
      /**
       * [handleSelect 选择某一个]
       * @return {[type]} [description]
       */
      handleSelect(val, row) {
        let hash = {}
        let checkedListTemp = []

        if(utils.isKeyInArrayBykey(row.uniqueId, 'uniqueId', this.checkedList)) {
          this.checkedList = utils.removeItemByKeyVal('uniqueId', row.uniqueId, this.checkedList)
          return
        }else {
          checkedListTemp.push(row)
        }

        let checkedListTempArr = [...checkedListTemp, ...this.checkedList]
        this.checkedList = checkedListTempArr.reduceRight((item, next) => {
          hash[next.uniqueId] ? '' : hash[next.uniqueId] = true && item.push(next)
          return item
        }, [])
      },
      /**
       * [handleSelectAll 选择多个]
       * @return {[type]} [description]
       */
      handleSelectAll(val) {
        let hash = {}
        if(val.length > 0) {        
          // 全选
          for(let item of val) {
            this.checkedList.push(item)
          }
          // 去重
          this.checkedList = this.checkedList.reduceRight((item, next) => {
            hash[next.uniqueId] ? '' : hash[next.uniqueId] = true && item.push(next)
            return item
          }, [])
        }else { 
          // 全不选 此时val.length<=0
          // 1.获取当前table中每一条的id
          let checkedListTemp = []
          for(let item of this.tableData) {
            checkedListTemp.push(item)
          }
          // 2.从要传递的参数中去掉这些重复的
          let checkedListArr = utils.getSubtract(this.checkedList, checkedListTemp, 'uniqueId')
          this.checkedList = checkedListArr && checkedListArr.length > 0 ? checkedListArr : [] 
        }
      },
      /**
       * [defauleSelectFun 设置表格中的默认选中项]
       * @param    {[type]}                 row [description]
       * @return   {[type]}                     [description]
       */
      defauleSelectFun() {
        let _this = this
        let rows = []
        for(let i of this.tableData) {
          for(let j of this.checkedList) {
            if(i.uniqueId === j.uniqueId) {
              i.platformCityId = j.platformCityId
              i.goodsId = j.goodsId
              i.goodsName = j.goodsName
              i.presalePrice = j.presalePrice
              i.price = j.price
              i.stock = j.stock
              i.platformCurStock = j.platformCurStock
              i.memberLimitNum = j.memberLimitNum
              i.disabledVal = j.disabledVal || false
              rows.push(i)
            }
          }
        }
        if (rows.length > 0) {
          this.$refs.multipleTable.clearSelection()
          setTimeout(function() {
            rows.forEach(rowItem => {
              _this.$refs.multipleTable.toggleRowSelection(rowItem)
            });
          }, 100)
        } else {
          this.$refs.multipleTable.clearSelection()
        }
      },
      /**
       * [confirmClose 选择并导入]
       * @return {[type]} [description]
       */
      confirmClose() {
        let _params = {
          checkedList: this.checkedList
        }
        this.$emit("confirmChooseGoods", _params)

        this.isShow = false
      },

      /**
       * [doCategorysChangeHandel 分类发生变化]
       * @return {[type]} [description]
       */
      doCategorysChangeHandel() {
        // 商品筛选组件初始化
        this.goodsSelectInit()

        this.doSearchHandel(true)
      },
      /**
       * [goodsSelectInit 商品筛选组件初始化]
       * @return {[type]} [description]
       */
      goodsSelectInit() {
        if(this.$refs.GoodsSelect) {
          let _val = {
            queryString: this.searchContent.queryString,
            categoryIdList: this.searchContent.categoryIdList,
            goodsIds: this.searchContent.goodsIds
          }
          this.$refs.GoodsSelect.setDefaultMesg(_val)
        }
      },
      /**
       * [DoSelectGoodsHandel 确认选择]
       */
      DoSelectGoodsHandel(_params) {
        let checkedList = _params.checkedList
        let queryString = _params.queryString
        let goodsIds = []
        for(let item of checkedList) {
          goodsIds.push(item.goodsId)
        }
        this.searchContent.goodsIds = goodsIds
        this.searchContent.queryString = queryString
        this.doSearchHandel(true)
      },
      selectable(row, index) {
        if(row.disabledVal === true){
          return false; 
        }else{
          return true;
        }
      }

    }
  }
</script>


